.slider_menu{position:fixed;z-index:2;height:100vh;left:0;overflow-y:auto}.slider_menu .logo_wrapper{padding-left:24px;height:64px;line-height:64px;overflow:hidden;background:hsla(0,0%,100%,.2);transition:all .3s}.slider_menu .logo{display:inline-block;width:32px;vertical-align:middle;cursor:pointer}.slider_menu h1.name{display:inline-block;color:#fff;font-weight:600;font-size:16px;font-family:Avenir,Helvetica Neue,Arial,Helvetica,sans-serif;vertical-align:middle;letter-spacing:2px}